.SceneHolder{
    background-image:linear-gradient(141deg, #9fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    width:100%;
    height:60vh;
    position: relative;
    overflow-x:hidden;
}

#bigMountain{
    position: absolute;
    z-index:1;
    height:25rem;
    bottom:0;
    left:5%;
}

#mediumMountain{
    position: absolute;
    z-index: 1;
    height: 20rem;
    bottom:0;
    right:15%;
}

#tallTree1{
    position: absolute;
    z-index:2;
    height:12rem;
    bottom:0;
    left:0;
    right:0;
}

#mediumTree1{
    position: absolute;
    z-index:2;
    height: 7rem;
    bottom:0;
    right:20%;    
}

#tallTree2{  
    position: absolute;
    z-index:3;
    height: 12rem;
    bottom:0;
    right:0;  
}

#mediumTree2{
    position: absolute;
    z-index:2;
    height: 8rem;
    bottom:0;
    left:20%;
}
